import React from "react";
import PropTypes from "prop-types";
import "./Movie.css";

function Movie({id, title, rating, year, summary, image, genres}) {
    return (
        <div className="movie">
            <img src={image} title={title} alt={title} />
            <div className="movie__info">
                <h3 className="movie__info__title">{title}</h3>
                <span className="movie__info__year">{year}</span>
                <ul className="movie__info__genres">
                    {genres.map((genre,index) => (
                        <li key={index} className="movie__info__genres__list">{genre}</li>
                    ))}
                </ul>
                <p className="movie__info__summary">
                    {summary.slice(0,200)}......
                </p>
            </div>
        </div>
    )
}

Movie.propTypes = {
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    rating: PropTypes.number.isRequired,
    year: PropTypes.number.isRequired,
    summary: PropTypes.string.isRequired,
    image: PropTypes.string.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired
}

export default Movie;